::-webkit-scrollbar {
   width: 0;
   height: 0;
   /*隐藏滚动条*/
}
#viewbox{
   width: 100%;
   height: 100%;
   overflow: auto;
}
.mainshow{
   /*z-index: -1;*/
   height: 10%;
   width: 100%;
   position: fixed;
}
.home+.Login>.weui-tabbar{
   height: 10%;
   position: fixed;
}
div  .weui-icon_toast.weui-loading{
  background-image:url(./assets/LOGO-1.png);
}
#app .weui-input{
   vertical-align:sub;
   height: .6rem;
   line-height: normal;
}
.weui-tabbar__item.weui-bar__item_on .weui-tabbar__label label{
   color:#3498db;
}
div .weui-tabbar{
   -webkit-box-align: center;
       -ms-flex-align: center;
           align-items: center;
   height: 100%;
   position: relative
}
.lf{
   float: left;
}
.rf{
   float: right;
}
label{
   font-weight: normal;
}
.mainshow  label{
   font:.35rem "Microsoft YaHei";
}
div p{
   margin: 0;
}
.vux-header-title span{
   font-size: .5rem;
}
.weui-label,#app input{
   font-size: .4rem;
}
div .weui-grid__icon{
   width: 40%;
   height: 40%;
}
.drak_bg{
    background: #f8f8f8 !important;
}
div .vux-header{
   background: #fff;
}
div .vux-header .vux-header-title{
   color: #000;
}
div .vux-number-selector,div .vux-number-input{
   height: 28px;
}
.border_bottom{
   border-bottom: 1px solid rgba(244,244,244,1);
}
.border_top{
   border-top: 1px solid rgba(244,244,244,1);
}
div .weui-toast__content{
   font-size: 0.4rem;
}
div .weui-cell__ft{
   display: -webkit-box;
