   display: flex;
   display: -webkit-flex;
   -webkit-box-align: center;
       -ms-flex-align: center;
           align-items: center;
}
div .weui-cell_access .weui-cell__ft:after{
   width: .2rem;
   height: .2rem;
}
div .weui-cells:before{
   border-top: none;
}
div .vux-cell-box:before{
   border-top: none;
}
.btn_login{
   width: 70%;
   position: fixed;
   left: 50%;
   bottom: 0.1rem;
   margin-left: -35%;
   z-index: 10;
}
.btn_login>button{
   background: #35a9fa;
   border: none;
   color: #fff;
   font-size: .45rem;
   border-radius: 20px;
}
div .weui-btn:after{
   border: none;
}
.bg_white{
   background: #ffffff;
   overflow: hidden;
   cursor: pointer;
   padding: .3rem 0;
}
.gray{
   color: #666 !important;
   cursor: pointer;
}
.dark{
   color: #999 !important;
}
.blue{
   color:#3498db !important;
   cursor: pointer;
}
.green{
   color: #09BB07;
}
.green_bg{
   background: green !important;
}
.red_bg{
   background: crimson !important;
}
.pink_bg{
   background: #eb304d !important;
}
.black{
   color: #000000;
}
body{
   fill: #666;
}
svg{
   fill: #666;
}
.red{
   color: red;
}
.mine_icon{
   width: .55rem;
}
.icon-red{
   width: 1rem;
}
.lg_icon{
   width: 1.5rem;
}
.maxmine_icon{
   width: 1.8rem;
   border-radius: 50%;
}
.maximg{
   width:3rem;
