}
 /*
 login
 */
.no-bordertop:before{
   border-top: none;
}
.m-img {
   padding-bottom: 14%;
   display: block;
   position: relative;
   max-width: 100%;
   background-size: cover;
   background-position: center center;
   border-radius: 2px;
}
div .weui-input{
   text-indent: .2rem;
}
div .vux-input-icon.weui-icon-warn:before, .vux-input-icon.weui-icon-success:before{
   /*font-size: 0;*/
}
div .weui-cell{
   padding: .4rem .5rem;
}
.btn_loginz{
   width: 70%;
   margin: 1rem 15% 0 15%;
}
.btn_loginz>button{
   background: #35a9fa;
   border: 1px solid #35a9fa;
   border-radius: 20px;
   color: #fff;
   font-size: .45rem;
   font-family: "Microsoft YaHei UI";
}
.help_msg{
   display: -webkit-box;
   display: -ms-flexbox;
   display: flex;
   display: -webkit-flex;
   -webkit-box-pack: justify;
       -ms-flex-pack: justify;
           justify-content: space-between;
   padding: .5rem ;
   -webkit-box-align: center;
       -ms-flex-align: center;
           align-items: center;
}
.right_icon{
   width: .6rem;
   height: .4rem;
}
div .weui-cell__ft{
   display: -webkit-box;
   display: -ms-flexbox;
   display: flex;
   display: -webkit-flex;
   -webkit-box-align: center;
       -ms-flex-align: center;
           align-items: center;
   -ms-flex-pack: distribute;
       justify-content: space-around;
   -webkit-box-orient: horizontal;
   -webkit-box-direction: reverse;
       -ms-flex-direction: row-reverse;
           flex-direction: row-reverse;
}
div .weui-cells:before{
   border-top: none;
}
.box_justify{
   padding: 1px 15px;
   background: #fff;
   margin: 0.2rem 0.3rem;
}
.justify{
   display: -webkit-box;
   display: -ms-flexbox;
   display: flex;
   display: -webkit-flex;
   -webkit-box-pack: justify;
       -ms-flex-pack: justify;
           justify-content: space-between;
   -webkit-box-align: center;
       -ms-flex-align: center;
           align-items: center;
   /*padding: .1rem .2rem;*/
   margin: 10px 15px;
}
.comment .vux-check-icon > .weui-icon-success:before, .vux-check-icon > .weui-icon-success-circle:before{
    color:#35a9fa !important;
}
.comment_box,.file_phone{
   overflow: hidden;
   float: left;
}
.comment_box>div,.file_phone{
   float: left;
   margin: 0 .15rem;
   width: 3rem;
   height: 3rem;
   cursor: pointer;
   position: relative;
}
.comment_box img{
   width: 3rem;
   height: 3rem;
}
.file_phone input{
   width: 3rem;
   height: 3rem;
   opacity: 0;
   -ms-filter: alpha(opacity=0);
   filter: alpha(opacity=0);
}

 /*shop*/
.pink_bg .vux-header-title{
   height: 1.2rem !important;
   line-height: 1.2rem !important;
}
.pink_bg.vux-header .vux-header-left .left-arrow{
   top: -0.1rem;
}
.pink_bg.vux-header .vux-header-left .left-arrow:before{
   border: 1px solid #fff;
   border-width: 2px 0 0 2px;
}
.footer {
   width: 100%;
   position: fixed;
   bottom: 0;
   display: -webkit-box;
   display: -ms-flexbox;
   display: flex;
   display: -webkit-flex;
   -webkit-box-pack: justify;
       -ms-flex-pack: justify;
           justify-content: space-between;
   -webkit-box-align: center;
       -ms-flex-align: center;
           align-items: center;
}
.footer a{
   font-size: .5rem;
}
.paycar {
   padding: .4rem 1rem;
   background: #eb304d;
   color: #ffffff;
}
.pink_bg span {
   color: #ffffff
}
[v-cloak] {
   display: none;
}



