
#app .home{
  height: 90%;
}
.home_box{
  /*display: flex;*/
  /*display: -webkit-flex;*/
  /*flex-direction: column;*/
  /*background: #f2f2f2;*/
}
.home .weui-grid{
  padding:.4rem 0
}
.relative{
  position: relative;
}
.home .weui-grids:before{
  border-top: none;
}
.home .weui-grids:after{
  border-left: none;
}
.home .weui-grid:before{
  border-right: none;
}
.home .weui-grid:after{
  border-bottom: none;
}
.home_address{
  width: 1rem;
  height: 1rem;
  border-radius: 50%;
  position: absolute;
  background: #fff;
  bottom: -.6rem;
  left: 50%;
  margin-left: -.5rem;
  line-height: 1rem;
}
.home_addressText{
  position: relative;
  padding-top: .6rem;
  padding-bottom: .3rem;
}
.home_addressText button{
  background: rgba(240,240,240,.5);
  border-radius: 10px;
  font-size: .35rem;
}
/*.loadingImg{*/
  /*width: 1rem;*/
/*}*/
/*div .bannerImg{*/
  /*width: 100%;*/
  /*height: 100%;*/
/*}*/
.autoImg img{
    display: block;
    width: 100%;
}
div .bannerImgerror {
  background-color: #666;
}
.bannerImgerror:after {
  content: '加载失败';
  color: red;
}
.weui-grid span{
  font-size: .35rem;
  color: #666;
}
.footer{
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
}
.text-scroll p{
  width: 80%;
  line-height: 30px;
  margin: auto;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}
div .vux-slider{
  overflow: inherit;
}



