
.contain {
  height: 90%;
  overflow: auto;
}
div .shop_button {
  -webkit-box-orient: horizontal;
  -webkit-box-direction: reverse;
      -ms-flex-direction: row-reverse;
          flex-direction: row-reverse;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  position: fixed;
  width: 100%;
  padding: 0;
  bottom: 0;
}
div .shop_button > div:nth-of-type(1) {
  padding: .4rem .7rem;
  background: red;
  color: #ffffff;
}
div .shop_button .weui-cell__ft.vux-cell-primary > div {
  width: 100%;
}
div .shop_button .vux-number-selector, div .shop_button .vux-number-input {
  height: 55px !important;
  line-height: 45px;
}
div .headertop {
  opacity: 0;
  width: 100%;
  position: fixed;
  top: 0;
  /*background: transparent;*/
  background: #fff;
}
.headericon {
  position: relative;
  vertical-align: inherit;
  z-index: 100;
  width: .7rem;
}



